body, html {
    height: 100%;
    background-color: white;
}

.App {
    text-align: center;
    height: 100%;
}

.App-logo {
    /*animation: App-logo-spin infinite 20s linear;*/
    height: 80px;
    width: 80px;
}

.App-header {
    background-color: #00171F;
    /*flex: 1;*/
    padding: 20px;
    color: white;
    -ms-flex-direction: row;
        flex-direction: row;
}

.App-title {
    font-size: 2.5em;
    text-align:center;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.linkBar {
    -ms-flex: 1 1;
        flex: 1 1;
    background-color: #00171F;
    padding: 10px;
}

.link {
    margin: 1px;
    padding: 10px;
    background-color: gray;
    border: 1em black;
    color: white;
}


